.SearchBar {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    background: white;
    border-radius: 6px;
    -webkit-transition: -webkit-box-shadow 0.1s;
    transition: -webkit-box-shadow 0.1s;
    -o-transition: box-shadow 0.1s;
    transition: box-shadow 0.1s;
    transition: box-shadow 0.1s, -webkit-box-shadow 0.1s;
}

.SearchBar svg {
    fill: #dddddd;
    -webkit-transition: fill 0.1s;
    -o-transition: fill 0.1s;
    transition: fill 0.1s;
}

.SearchBar.--focused {
    -webkit-box-shadow: 0 0 0 3px #8C7642;
            box-shadow: 0 0 0 3px #8C7642;
}

.SearchBar.--focused svg {
    fill: #8C7642;
}

.SearchBar label {
    width: 32px;
    height: 32px;
    padding-left: 6px;
}

.SearchBar input {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    -ms-flex: 1 1 auto;
        flex: 1 1 auto;
    font-size: 18px;
    padding: 8px;
    border: 0;
    border-radius: 6px;
    outline: 0;
}


